body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
  color: #333;
}

h1 {
  color: #007BFF;
  text-align: center;
}

h2 {
  color: #ff9900;
 font-size: xx-large;
 text-align: center;
}

header, footer {
  background-color: #f1f1f1;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

th {
  background-color: #007BFF;
  color: white;
}

button {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

input[type="number"], input[type="text"] {
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
}


.suggestions {
  position: absolute;
  background: white;
  border: 1px solid #ccc;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  z-index: 10;
  max-height: 150px;
  overflow-y: auto;
}

.suggestions li {
  padding: 5px;
  cursor: pointer;
}

.suggestions li:hover {
  background: #f0f0f0;
}


.logo {
  height: 100px; /* Ajusta el tamaño del logo según lo necesites */
  margin-right: 200px;
}